import request from '../../utils/request'
import qs from 'querystring'

const state = {
  myApprovePunishmentNum: 0
}

const getters = {}

const actions = {
  getMyApprovePunishmentNum({ commit, state }, id) {
    // TODO 使用真实的接口地址
    request(`/api/tPunishment/getMyApprovePunishmentNum`, {
      method: 'POST'
    })
      .then(response => {
        if (response.resultCode === 200) {
          commit('GET_MYAPPROVE_PUNISHMENT_NUM_SUCCESS', { response })
        } else {
          commit('GET_MYAPPROVE_PUNISHMENT_NUM_FAILD', { response })
        }
      })
      .catch(err => {
        console.log(err)
      })
  }
}

const mutations = {
  GET_MYAPPROVE_PUNISHMENT_NUM_SUCCESS(state, { response }) {
    const { data } = response
    state.myApprovePunishmentNum = data['待审核数量']
  },
  GET_MYAPPROVE_PUNISHMENT_NUM_FAILD(state, { response }) {

  }
}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}
