<template>
  <div class="main">
    <keep-alive>
      <router-view v-if="$route.meta.keepAlive">
        <!-- 这里是会被缓存的视图组件 -->
      </router-view>
    </keep-alive>

    <router-view v-if="!$route.meta.keepAlive">
      <!-- 这里是不被缓存的视图组件 -->
    </router-view>

    <van-tabbar v-model="activeTabIndex">
      <van-tabbar-item icon="orders-o" to="punish" replace>违章处罚</van-tabbar-item>
      <van-tabbar-item icon="search" to="search" replace>查询</van-tabbar-item>
      <van-tabbar-item icon="certificate" to="approval" :info="myApprovePunishmentNum > 0 ? myApprovePunishmentNum : undefined" replace>审核审批</van-tabbar-item>
      <van-tabbar-item icon="contact" to="my" replace>我的</van-tabbar-item>
    </van-tabbar>
  </div>
</template>

<script>
import Vue from 'vue'
import { mapState, mapGetters, mapActions } from 'vuex'
import { Tabbar, TabbarItem } from 'vant'

Vue.use(Tabbar).use(TabbarItem)

const tabNames = ['punish', 'search', 'approval', 'my']

export default {
  name: 'home',
  components: {},
  data() {
    return {
    }
  },
  computed: {
    ...mapState({
      myApprovePunishmentNum: state => state.approval.myApprovePunishmentNum
    }),
    activeTabIndex: {
      get() {
        return tabNames.indexOf(this.$route.name)
      },
      set(value) {}
    }
  },
  created() {
    this.getMyApprovePunishmentNum()
  },
  methods: {
    ...mapActions('approval', ['getMyApprovePunishmentNum'])
  }
}
</script>

<style lang="less" scoped>
.van-tabbar-item {
  position: relative;
  &::after {
    content: ' ';
    pointer-events: none;
    display: inline-block;
    position: absolute;
    top: 16px;
    right: 0;
    width: 1px;
    height: 20px;
    background-color: #eee;
  }
  &:last-child::after {
