import Vue from 'vue'
import VueRouter from 'vue-router'
import Main from '../views/Main.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    component: Main,
    children: [
      {
        path: '',
        redirect: 'punish'
      },
      {
        path: 'punish',
        name: 'punish',
        component: () => import('../views/Punish'),
        meta: {
          title: '违章处罚'
        }
      },
      {
        path: 'search',
        name: 'search',
        component: () => import('../views/Search'),
        meta: {
          title: '查询'
        }
      },
      {
        path: 'approval',
        name: 'approval',
        component: () => import('../views/Approval'),
        meta: {
          title: '审核审批'
        }
      },
      {
        path: 'my',
        name: 'my',
        component: () => import('../views/My'),
        meta: {
          title: '我的'
        }
      }
    ]
  },
  {
    path: '/inner-test',
    name: 'inner-test',
    component: () => import('../views/InnerTestPage.vue'),
    meta: {
      title: '登录'
    }
  },
  {
    path: '/illegal-info',
    name: 'illegal-info',
    component: () => import('../views/IllegalInfo.vue'),
    meta: {
      title: '违法信息',
      keepAlive: true
    }
  },
  {
    path: '/illegal-punish',
    name: 'illegal-punish',
    component: () => import('../views/IllegalPunish.vue'),
    meta: {
      title: '违法处罚',
      keepAlive: true
    }
  },
  {
    path: '/approval-detail',
    name: 'approval-detail',
    component: () => import('../views/ApprovalDetail.vue'),
    meta: {
      title: '详情'
    },
    props: true
  },
  {
    path: '/identity-check',
    name: 'identity-check',
    component: () => import('../views/IdentityCheck.vue'),
    meta: {
      title: '身份核查结果'
    }
  },
  {
    path: '/login',
    name: 'login',
    component: () => import('../views/Login.vue'),
    meta: {
      title: '登录'
    }
  }
  // {
  //   path: '/register',
  //   name: 'register',
  //   component: () => import('../views/Register.vue'),
  //   meta: {
  //     title: '注册'
  //   }
  // }
]

const router = new VueRouter({
  routes,
  scrollBehavior(to, from, savedPosition) {
    if (savedPosition) {
      return savedPosition
    } else {
      return { x: 0, y: 0 }
    }
  }
})

export default router
