import request from '../../utils/request'
import qs from 'querystring'

const state = {
  userInfo: {}
}

const getters = {}

const actions = {
  getLoginUserInfo({ commit }) {
    request(`/api/getLoginUserInfo`, {
      method: 'POST'
    }).then(response => {
      if (response.resultCode === 200) {
        commit('GET_USER_INFO_SUCCESS', response)
      } else {
        commit('GET_USER_INFO_FAILED', response)
      }
    })
  }
}

const mutations = {
  GET_USER_INFO_SUCCESS(state, response) {
    state.userInfo = response.data.user
  },
  GET_USER_INFO_FAILED(state, response) {
    console.log('获取登录用户信息失败')
  }
}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}
