/**
 * request 网络请求工具
 * 更详细的 api 文档: https://github.com/umijs/umi-request
 */
import Vue from 'vue'
import { extend } from 'umi-request'
import { Notify } from 'vant'
import router from '../router'
import { getToken, removeToken } from './token'

Vue.use(Notify)

export const host = 'http://222.84.157.37:43001' // 开发
// export const host = 'http://219.159.250.198:8821' // 部署
// const host = ''

const codeMessage = {
  200: '服务器成功返回请求的数据。',
  201: '新建或修改数据成功。',
  202: '一个请求已经进入后台排队（异步任务）。',
  204: '删除数据成功。',
  400: '发出的请求有错误，服务器没有进行新建或修改数据的操作。',
  401: '用户没有权限（令牌、用户名、密码错误）。',
  403: '用户得到授权，但是访问是被禁止的。',
  404: '发出的请求针对的是不存在的记录，服务器没有进行操作。',
  406: '请求的格式不可得。',
  410: '请求的资源被永久删除，且不会再得到的。',
  422: '当创建一个对象时，发生一个验证错误。',
  500: '服务器发生错误，请检查服务器。',
  502: '网关错误。',
  503: '服务不可用，服务器暂时过载或维护。',
  504: '网关超时。'
}

/**
 * 异常处理程序
 */
const errorHandler = error => {
  const { response = {} } = error
  const errortext = codeMessage[response.status] || response.statusText
  const { status, url } = response

  Notify({
    message: `请求错误 ${status}: ${url}## ${errortext}`,
    duration: 1000,
    background: '#1989fa'
  })
}

/**
 * 配置request请求时的默认参数
 */
const request = extend({
  errorHandler, // 默认错误处理
  // credentials: 'include' // 默认请求是否带上cookie
  headers: {
    // token: window.localStorage.getItem('token') // 统一的headers
  }
})

request.interceptors.request.use((url, options) => {
  const noNeedAuthorizationUrls = ['checkCode/sendPhoneMsgCheckCode', 'auth/fujingLoginByPhoneCheckCode'] // 不需要登录就可以访问的接口
  const isNoNeedAuthorizationUrl =
    noNeedAuthorizationUrls.findIndex(item => url.indexOf(item) > -1) > -1
  const authorization = getToken()
  if (authorization && !isNoNeedAuthorizationUrl) {
    options.headers.Authorization = authorization
  }
  return {
    url: `${host}${url}`,
    options: { ...options }
  }
})

request.interceptors.response.use(async response => {
  const data = await response.clone().json()
  if (data.resultCode === 401) {
    removeToken()
    router.replace({
      name: 'login'
    })
  }
  return response
})

export default request
