<template>
  <div id="app">
    <div v-if="isReady || isNoNeedAuthPage">
      <navigation>
        <router-view></router-view>
      </navigation>
    </div>
    <div v-else class="loading-wrap">
      <van-loading type="spinner">加载中...</van-loading>
    </div>
  </div>
</template>

<script>
import Vue from 'vue'
import { NavBar, Image, Loading, Dialog } from 'vant'
import qs from 'querystring'
import { mapState, mapGetters, mapActions } from 'vuex'
import store from './store'
import Navigation from 'vue-navigation'
import request from '@/utils/request'
import router from './router'
import { getToken } from '@/utils/token'

Vue.use(NavBar)
  .use(Image)
  .use(Loading)
  .use(Dialog)
  .use(Navigation, { router, store })

// 全局混入
Vue.mixin({
  computed: {},
  methods: {
    // 可以在这里 mapActions
    ...mapActions({
      // getApprovalList: 'approval/getList'
    }),

    // 左边的返回按钮
    onClickNavBarLeft() {
      this.$router.go(-1)
    }
  }
})

export default {
  data() {
    return {}
  },
  computed: {
    ...mapState({
      userId: state => state.login.userInfo.id
    }),
    isReady() {
      return this.userId && getToken()
    },
    isNoNeedAuthPage() {
      return ['login', 'register'].indexOf(this.$route.name) > -1
    }
  },
  methods: {
    ...mapActions('login', ['getLoginUserInfo'])
  },
  created() {
    if (!getToken()) {
      this.$router.replace({
        name: 'login'
      })
    } else {
      this.getLoginUserInfo()
    }
  }
}
</script>

<style lang="less">
#app {
  min-height: 100vh;
}

.loading-wrap {
  padding: 100px 0;
  text-align: center;
}

.van-nav-bar {
  background-color: #456fe4;
  &::after {
    border-color: #456fe4;
  }
  .van-icon {
    color: #fff;
  }

  .van-nav-bar__title {
    color: #fff;
  }
}
</style>
