import Vue from 'vue'
import _ from 'underscore'

Vue.mixin({
  methods: {
    // 是否为数字
    isNumber(value) {
      const parsed = parseFloat(value)
      return !_.isNaN(parsed) && _.isNumber(parsed)
    },
    // 搜索关键字高亮
    keyLight(text) {
      if (!this.keyWord || !this.keyWord) {
        return text
      }
      const result = text.replace(
        new RegExp(this.keyWord, 'g'),
        `<span class="hilight">${this.keyWord}</span>`
      )
      return result
    },
    valuesHtml(value) {
      if (!value) return ''
      if (_.isArray(value)) {
        return value.join('<br />')
      }
      return value
    }
  }
})
